@use 'base/variables' as v;
@use 'base/mixins' as m;

.senara-content-login {
  position: relative;
  height: 100vh;
  //padding: 2rem;
  display: flex;
  background: v.$bgGris;
  overflow-x: auto;

  //height: 95vh;
  //max-width: 1200px;
  //margin: 0 auto;
}

.senara-content-sm-login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 55rem;
  max-width: 50rem;
  margin: auto;
  padding: 3rem;
  height: 70rem;
  //border: 1px solid rgba(85, 91, 82, 0.44);
  background: v.$blanco;
  -webkit-box-shadow: v.$webkit;
  -moz-box-shadow: v.$moz;
  box-shadow: v.$boxShadow;

  @include m.desktop {
    //height: 55rem;
    width: 60rem;
  }
}

.senara-content-sm-login.register-form {
  max-height: 70rem;
}

.senara-logo {
  margin: 0 auto;
  width: 20rem;
  height: 10rem;
  display: flex;

  .senara-img-logo {
    width: 100%;
    background-image: url('../img/logo_letra.png');
    background-size: cover;
    background-position: center center;
  }
}

.senara-footer-decoration {
  width: 10rem;
  height: 20rem;
  position: absolute;
  right: 0;
  bottom: 0;
  display: none;

  .decoration-logo {
    width: 100%;
    height: 100%;
    background: url('../img/adorno_senara.png') no-repeat bottom right
      transparent;
    background-size: cover;
    background-position: center center;
  }

  @include m.desktop {
    display: block;
  }
}

.form-login {
  padding: 0 3rem;
}

.senara-content-legend-auth {
  display: flex;
  align-content: center;
  flex-direction: column;
  margin-top: 4rem;
  margin-bottom: 4rem;
}
