#spinner {
  position: absolute;
  background-color: rgba(243, 244, 246, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  display: none;
}

.senara-loader {
  animation: spin 1s infinite linear;
  border: solid 2vmin transparent;
  border-radius: 50%;
  border-right-color: #07392a;
  border-top-color: #07392a;
  box-sizing: border-box;
  height: 16vmin;
  left: calc(50% - 10vmin);
  position: fixed;
  top: calc(50% - 10vmin);
  width: 16vmin;
  z-index: 1;
}
.senara-loader:before {
  animation: spin 2s infinite linear;
  border: solid 2vmin transparent;
  border-radius: 50%;
  border-right-color: #17568e;
  border-top-color: #17568e;
  box-sizing: border-box;
  content: "";
  height: 12vmin;
  left: 0;
  position: absolute;
  top: 0;
  width: 12vmin;
}
.senara-loader:after {
  animation: spin 3s infinite linear;
  border: solid 2vmin transparent;
  border-radius: 50%;
  border-right-color: #1480c0;
  border-top-color: #1480c0;
  box-sizing: border-box;
  content: "";
  height: 8vmin;
  left: 2vmin;
  position: absolute;
  top: 2vmin;
  width: 8vmin;
}

.spinner-loading {
  place-self: center;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.spinner-loading div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.spinner-loading div:nth-child(1) {
  left: 8px;
  background: #5d923a;
  animation: spinner-loading1 0.6s infinite;
}

.spinner-loading div:nth-child(2) {
  left: 8px;
  background: #267438;
  animation: spinner-loading2 0.6s infinite;
}

.spinner-loading div:nth-child(3) {
  left: 32px;
  background: rgb(0, 120, 0);
  animation: spinner-loading2 0.6s infinite;
}

.spinner-loading div:nth-child(4) {
  left: 56px;
  background: #07392a;
  animation: spinner-loading3 0.6s infinite;
}

@keyframes spinner-loading1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes spinner-loading3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes spinner-loading2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
