#senara-table {
  width: 100%;
  border-collapse: collapse;

  > thead {
    > tr {
      > th {
        font-size: 1.5rem;
      }
    }
  }

  > tbody {
    position: relative;

    //height: 36rem;
    > tr {
      > td:last-child {
        display: flex;
        justify-content: space-around;

        > i {
          padding: 0.5rem;
          cursor: pointer;

          &:hover {
            background-color: lighten(v.$gris, 30%);
          }
        }

        > .fa-pen-to-square {
          color: v.$azulOscuro;
        }

        > .fa-trash-can {
          color: v.$rojo;
        }
      }
    }
  }
}

#senara-table th {
  background: darken(v.$azulClaro, 25%);
  font-size: 1.5rem;
  font-weight: 400;
  color: v.$blanco;
  border-bottom: solid thin #c1c1c1;
  border-right: solid thin #c1c1c1;
  border-top: solid thin #d6d6d6;
  border-left: solid thin #d6d6d6;
}

#senara-table td {
  // padding: 4px 8px;
  font-size: 1.5rem;
  font-weight: 400;
  color: #474443;
  border-bottom: solid thin #c1c1c1;
  border-right: solid thin #c1c1c1;
  border-top: solid thin #d6d6d6;
  border-left: solid thin #d6d6d6;
}

