.content-table-senara {
  display: block;
  height: 58%;
  overflow-y: scroll;
  padding: 2rem 1rem 2rem 0;
  margin-top: 2rem;
}

.status-col {
  padding: 0.2rem 0.8rem;
  border-radius: 8px;
  cursor: pointer;
  display: block;
  text-align: center;
  width: 9rem;

  &.active {
    background-color: v.$verdeMedio;
    color: v.$blanco;
  }

  &.desactive {
    background-color: darken(v.$naranja, 10%);
    color: v.$blanco;
  }
}

.senara-form-group.crops {
  width: 100%;
  overflow-x: auto;

  .table-container {
    padding: 1rem 0;

    #siao-table {
      width: 100%;
      border-collapse: collapse;

      thead {
        background-color: lighten(v.$gris, 35%);
      }

      th {
        padding: 0.5rem;
      }

      thead,
      tbody {
        tr {
          display: grid;
          grid-template-columns: repeat(4, minmax(150px, 1fr)) 100px;
        }

        th,
        td {
          border: 1px solid black;
        }

        td {
          margin: 0;
          padding: 0;

          input {
            text-align: center;
            border: 0;
            width: 100%;
            padding: 0.5rem;
          }
        }
      }

      tbody > tr > td {
        div {
          display: grid;
          width: 100%;
          height: 100%;
          cursor: pointer;

          a {
            display: grid;
            place-content: center;
          }

          &:hover {
            background-color: lighten(v.$gris, 35%);
          }
        }
      }

      tfoot > tr {
        border: none;

        > td {
          display: grid;
          justify-items: end;
          margin: 0;
          padding: 0;

          input[type='button'].table-btn {
            padding: 0.5rem;
            border-radius: 0;
            width: 40px;
          }
        }
      }
    }
  }
}
